import React from 'react';
import logo from '.././logo.svg';
import textlogo from '.././textlogo.svg';
import user from '.././user.svg';

const Navbar = ()=>{
	return(
		   <nav className="navbar navbar-expand-lg navbar-light pt-4 pb-3 shadow-sm">
			  <div className="container">
			    <a className="navbar-brand" href="#"><img src={textlogo} className="pr-3"/><span></span></a>
			    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
			      <span className="navbar-toggler-icon"></span>
			    </button>
			    <div className="collapse navbar-collapse" id="navbarSupportedContent">
			      <ul className="navbar-nav me-auto mb-2 mb-lg-0 left">
			        <li className="nav-item  me-5 active">
			          <a className="nav-link"href="#">jeetar</a>
			        </li>
			         <li className="nav-item me-5">
			          <a className="nav-link" href="#">jeetarhealth</a>
			        </li>
			         <li className="nav-item">
			          <a className="nav-link" href="#">jeetarbusiness</a>
			        </li>
			        
			      </ul>

			      <ul className="navbar-nav ml-auto mb-2 mb-lg-0 right">
			        <li className="nav-item me-5">
			          <a className="nav-link text-primary" href="#">Login</a>
			        </li>
			        <li className="nav-item">
			          <button type="button" className="btn btn-primary sign-up-btn" data-bs-toggle="modal" data-bs-target="#signupModal"><img src={user} className="px-1"/>Sign Up</button>
			        </li>
			      </ul>
			      
			    </div>
			  </div>
			</nav>
		);
}

export default Navbar